import router from "./router/index";
import store from "./store/index";
import NProgress from "nprogress"; // Progress 
import "nprogress/nprogress.css"; // Progress 
import { getAdminId } from "./utils/auth"; // 
import { Message } from "element-ui";
import { asyncRouterMap } from "./router/index";
// permissiom judge
function hasRole(authRules, permissionAuthRules) {
    if (!authRules || authRules.length <= 0) {
        return false;
    }
    if (authRules.indexOf("admin") >= 0) return true; // admin 
    if (!permissionAuthRules) return true;
    return authRules.some(role => permissionAuthRules.indexOf(role) >= 0);
}

/**
 * meta.role
 * @param authRules
 * @param route
 */
function hasRouterRole(authRules, route) {
    if (
        authRules.indexOf("admin") >= 0 ||
        !route.meta ||
        !route.meta.authRule
    ) {
        return true;
    }
    return authRules.some(
        authRule => route.meta.authRule.indexOf(authRule) >= 0
    );
}

/**
 * 
 * @param asyncRouterMap
 * @param authRules
 */
function filterAsyncRouter(asyncRouterMap, authRules) {
    const accessedRouters = asyncRouterMap.filter(route => {
        if (hasRouterRole(authRules, route)) {
            if (route.children && route.children.length) {
                route.children = filterAsyncRouter(route.children, authRules);
            }
            return true;
        }
        return false;
    });
    return accessedRouters;
}

// register global progress.
const whiteList = ["/login", "/authredirect"]; // 
router.beforeEach((to, from, next) => {
    NProgress.start(); // Progress
    let adminId = getAdminId();
    if (adminId !== "undefined" && adminId !== "" && adminId) {
        // token
        // token
        if (to.path === "/login") {
            next({ path: "/" });
            NProgress.done(); // routerhash hash  afterEach hack pshistory
        } else {
            if (
                !store.getters.userName &&
                (!store.getters.authRules ||
                    store.getters.authRules.length === 0)
            ) {
                // 
                store
                    .dispatch("userInfo")
                    .then(res => {
                        // user_info
                        const authRules = res.authRules || [];
                        if (
                            !(authRules instanceof Array) ||
                            authRules.length === 0
                        ) {
                            Message.error("~");
                            store.dispatch("loginOut").then(() => {
                                let redirect = to.fullPath;
                                next({
                                    path: "/login",
                                    query: { redirect: redirect }
                                });
                            });
                            NProgress.done();
                        }
                        let accessedRouters = filterAsyncRouter(
                            asyncRouterMap,
                            authRules
                        );
                        // 
                        router.addRoutes(accessedRouters); // 
                        next({ ...to }); // hack addRoutes
                        // 
                        store
                            .dispatch("filterRouter", { accessedRouters })
                            .then(() => {});
                    })
                    .catch(() => {
                        store.dispatch("fedLogout").then(() => {
                            Message.error(",");
                            let redirect = to.fullPath;
                            store.dispatch("loginOut").then(() => {
                                next({
                                    path: "/login",
                                    query: { redirect: redirect }
                                });
                            });
                        });
                    });
            } else {
                // next()  
                if (hasRole(store.getters.authRules, to.meta.authRule)) {
                    next(); //
                } else {
                    next({
                        path: "/401",
                        query: { noGoBack: true }
                    });
                    NProgress.done(); // routerhash hash  afterEach hack pshistory
                }
                //  
            }
        }
    } else {
        if (whiteList.indexOf(to.path) !== -1) {
            // 
            next();
        } else {
            let redirect = to.fullPath;
            store.dispatch("loginOut").then(() => {
                next({
                    path: "/login",
                    query: { redirect: redirect }
                });
            }); // 
            NProgress.done(); // routerhash hash  afterEach hack pshistory
        }
    }
});

router.afterEach(() => {
    NProgress.done(); // Progress
});
